import uniRequest from 'uni-request';
export default {
	data() {
		return {
			datas: [],
			count: 1,
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			price: 0,
			disPrice:0, // 优惠价
			code:true,
			invoice: '个人 不开发票',
			goods_id:'',
			invoiceList:[
				{
					title:'个人 不开发票',
					invoice_type: '0', //发票类型  //"0"
				},
				{
					title:'普通发票',
					invoice_type: '1', //发票类型  //"0"
				},
				{
					title:'增值税发票',
					invoice_type: '2', //发票类型  //"0"
				},
				{
					title:'电子发票',
					invoice_type: '3', //发票类型  //"0"
				},
			],
			couponList: [], 
			generalVote:[], // 普票
			increaseTicket:[], // 增票
			addressData: {
				title: '选择收货地址',
			},
			current:null,
			info: {
				address: this.addressData === undefined ? null : this.addressData.id,
				pay_method: null, // 1
				// 央采字段开始
				name: '',
				invoice: null, //发票id
				invoice_type: '0', //发票类型  //"0"
				invoice_title: "", //发票抬头
				invoice_org_code: '', //纳税人识别号
				invoice_phone: '', // 注册电话
				invoice_back_code: '', //开户银行账号
				invoice_bank: '', // 注册银行
			}
		}
	},
	onShow(){
		/* if(this.addressData.title !== '选择收货地址'){
			this.addressData
		} */
	},
	onLoad(option) {
		if(uni.getStorageSync('userInfo').code){
			this.code = true
		}else{
			this.code = false
		}
		let data = JSON.parse(option.data)
		console.log(data)
		//商品数据
		if (option.count) {  // 直接购买
			this.datas = data
			this.datas.count = option.count
			this.price = this.datas.price
			this.goods_id = option.goods_id
		} else { // 购物车购买
			this.datas = data
			console.log(this.datas)
			let price
			let disPrice
			const priceArr = []
			const disPriceArr = []
			this.datas.forEach(ele => {
				price = Number(ele.price) * ele.count
				priceArr.push(price)
				disPrice = ele.dis_price * ele.count
				disPriceArr.push(disPrice)
			})
			priceArr.forEach(ele => {
				this.price += ele
			})
			disPriceArr.forEach(ele => {
				this.disPrice += ele
			})
			this.price = this.price.toFixed(2)
			this.disPrice = (+this.price - this.disPrice).toFixed(2)
		}
		this.getInvoice()
	},
	methods: {
		//显示发票面板
		toggleMask(type) {
			console.log(type)
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			setTimeout(() => {
				this.maskState = state;
			}, timer)
		},
		setInvoice(type,item) {
			console.log(item)
			if(type === 'info'){
				this.maskState = 0;
				this.invoice = item.title
				this.info.invoice_type = item.invoice_type
				this.info.invoice = item.id
				this.info.invoice_bank = item.invoice_bank
				this.info.invoice_bank_code = item.invoice_bank_code
				this.info.invoice_org_code = item.invoice_org_code
				this.info.invoice_phone = item.invoice_phone
				this.info.invoice_title = item.invoice_title
				this.info.register_address = item.register_address
			}else if(type === 'check'){
				this.info.invoice = item.id
				this.info.invoice_title = item.invoice_title
				this.info.invoice_bank = item.invoice_bank
				this.info.invoice_bank_code = item.invoice_bank_code
				this.info.invoice_org_code = item.invoice_org_code
				this.info.invoice_phone = item.invoice_phone
				this.info.invoice_title = item.invoice_title
				this.info.register_address = item.register_address
			}
		},
		async getInvoice() {
			const res = await uniRequest({
				url: '/orders/invoice/',
				method: 'get',
				headers: {
					Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
				},
			}).then(res => {
				console.log(res)
				this.couponList = res.data
				this.couponList.forEach(ele=>{
					this.info.invoice = ele.id
					if(ele.invoice_bank !== null){
						// this.checked.push(ele.checked)
						this.increaseTicket.push(ele)
					}else{
						this.generalVote.push(ele)
					}
					console.log(this.checked)
				})
				console.log(this.increaseTicket,this.generalVote)
				if (this.couponList.length === 0) {
					console.log('000')
				} else {
					console.log('111')
				}
			}).catch(error => {

			})
		},
		
		radioChange(e){
			console.log(e.detail.value)
			const index = e.detail.value.split('-')[0]
			const type = +e.detail.value.split('-')[1]
			console.log(index,type)
			if(type === 1 || +type === 3){
				this.setInvoice('check',this.generalVote[index])
			}else if(type === 2){
				this.setInvoice('check',this.increaseTicket[index])
			}
		},
		
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(index,data) {
			console.log(index,data)
			// this.payType = type;
			// this.setInvoice('check',this.data[index])
		},
		submit() {
			console.log(this.info)
			if(this.addressData.id !== undefined){
				this.info.address = this.addressData.id
			}
			if(this.info.address === null){
				this.$api.msg('请选择收货地址')
				return
			}
			
			 
			if(this.info.invoice === null){
				this.info.invoice = this.invoice
				this.info.invoice_type = '0'
			}
			
			if(this.goods_id !== undefined || this.datas.count !== undefined){
				
				if(!this.code){
					uni.redirectTo({
						url: '/pages/money/pay?info=' + JSON.stringify(this.info) + '&price='+ this.price + '&goods_id='+ this.goods_id + '&count='+this.datas.count
					})
				}else{
					uni.redirectTo({
						url: '/pages/money/pay?info=' + JSON.stringify(this.info) + '&price='+ this.price +'&disPrice='+ this.disPrice + '&goods_id='+ this.goods_id + '&count='+this.datas.count
					})
				}
			}else{
				uni.redirectTo({
					url: '/pages/money/pay?info=' + JSON.stringify(this.info) + '&price='+ this.price
				})
			}
			
		},
		stopPrevent(e) {

		}
	}
}
